.Home {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: rgb(0, 0, 0);
    width: 100%;
    padding-top: 20px;
    
}

.Home .btn-plot {

    position: relative;
    
    margin-top: 30px;
    color: #090909;
    padding: 0.7em 1.7em;
    font-size: 18px;
    border-radius: 0.5em;
    background: #e8e8e8;
    border: 1px solid #e8e8e8;
    transition: all .3s;
    box-shadow: 6px 6px 12px #c5c5c5,
               -6px -6px 12px #ffffff;
  }
  
.Home  .btn-plot:hover {
    border: 1px solid white;
  }
  
.Home  .btn-plot:active {
    box-shadow: 4px 4px 12px #c5c5c5,
               -4px -4px 12px #ffffff;
  }



  

