.Banner {
    width: 100%;
    display: inline-flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    padding: 5px;
    background-image: linear-gradient(90deg,  #FA709A 20%,#feb840 100%);

}

.Banner div h1 {
    display: flex;
    flex-wrap: none;
    font-family: montserrat;
    font-style: bold;
    font-size: 30px;
    color: black;

}

.Banner .scattering-logo{

    width: 100px;
    height: 100px;
}

.Banner button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-family: inherit;
    font-size: 13px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.4px;
    color: #ffffff;
    background : linear-gradient(90deg,  #FA709A 20%,#feb840 100%);
    
    border-style: solid;
    border-width: 2px 2px 2px 2px;
    border-color: rgba(255, 255, 255, 0.715);
    border-radius: 40px 40px 40px 40px;
    padding: 12px 24px 12px 28px;
    transform: translate(0px, 0px) rotate(0deg);
    transition: 0.2s;
    box-shadow: -4px -2px 16px 0px #ffffff, 4px 2px 16px 0px rgb(95 157 231 / 48%);
    
   }
   
.Banner button:hover {
    color: #516d91;
    background-color: #E5EDF5;
    box-shadow: -2px -1px 8px 0px #ffffff, 2px 1px 8px 0px rgb(95 157 231 / 48%);
   }
   
.Banner button:active {
    box-shadow: none;
   }